html {
  font-size: 20px;
}
:root {
  --color-black: #000;
  --color-dark-gray: #202020;
  --color-gray: #303030;
  --color-middle-gray: #606060;
  --color-light-gray: #c0c0c0;
  --color-blue: #0072ec;
  --color-light-blue: #adcef2;
  --color-red: #cc0001;
  --color-white: #fff;
}
* {
  font-family: "Noto Sans KR", sans-serif;
  margin: 0;
  box-sizing: border-box;
}
a {
  text-decoration: none;
  color: inherit;
}
button {
  border: none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
}
body {
  background: var(--color-black);
}
.main-content {
  margin-left: 4rem;
  margin-top: 3.7rem;
}
.list {
  padding: 1.5rem 0 0 0.7rem;
}
.content {
  display: flex;
  margin-top: 2.7rem;
}
.watchList {
  padding-left: 0;
  flex: 1 1 35%;
  flex-wrap: wrap;
}

@media screen and (max-width: 680px) {
  .content {
    flex-direction: column;
    margin-top: 3rem;
  }
}
